<template>
  <div class="sex-chart-container">
    <!--  ECharts  DOM -->
    <div ref="main" class="main"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
export default {
  name: 'SexChart',
  data() {
    return {}
  },
  mounted() {
    // domecharts
    const myChart = echarts.init(this.$refs.main)
    // 
    const option = {
      title: {
        text: '', // 
        textStyle: {
          color: '#000', // 
          fontStyle: 'normal', // 
          fontWeight: 'normal', // 
          fontFamily: 'Microsoft yahei', // 
          fontSize: 14, // 
          align: 'center' // 
        }
      },
      grid: {
        left: '2%',
        right: '2%',
        bottom: '2%',
        containLabel: true
      },
      tooltip: {
        trigger: 'item'
      },
      legend: {
        bottom: '5%',
        left: 'center'
      },
      series: [
        {
          type: 'pie',
          radius: ['40%', '70%'],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 8,
            borderColor: '#fff',
            borderWidth: 2
          },
          label: {
            show: false,
            position: 'center'
          },
          emphasis: {
            label: {
              show: true,
              fontSize: '20'
            }
          },
          labelLine: {
            show: false
          },
          data: [
            { value: 0, name: '' },
            { value: 0, name: '' },
            { value: 0, name: '' }
          ]
        }
      ]
    }
    // 
    myChart.setOption(option)
    // 
    window.addEventListener('resize', function () {
      myChart.resize()
    })
  }
}
</script>

<style lang="less" scoped>
.sex-chart-container {
  width: 100%;
  height: 100%;
  .main {
    width: 100%;
    height: 100%;
  }
}
</style>
