<template>
  <div class="publish-container">
    <!--  -->
    <div class="article-pub-card">
      <el-card shadow="never">
        <div slot="header" class="clearfix">
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item>{{ $route.query.id ? '' : '' }}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div>
          <el-form ref="publish-from" :rules="articleRules" :model="article" label-width="80px" size="small">
            <el-form-item label="" prop="title">
              <el-input placeholder="" v-model="article.title" maxlength="30" show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="" prop="content">
              <el-tiptap
                class="tiptap-warp"
                v-model="article.content"
                :charCounterCount="false"
                :extensions="extensions"
                lang="zh"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label="" prop="channel_id">
              <el-select v-model="article.channel_id" placeholder="">
                <el-option v-for="channel in channels" :key="channel.id" :label="channel.name" :value="channel.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="article.cover.type">
                <el-radio :label="1"></el-radio>
                <el-radio :label="3"></el-radio>
                <el-radio :label="0"></el-radio>
                <el-radio :label="-1"></el-radio>
              </el-radio-group>
              <!--  -->
              <!-- <div class="upload-cover-warp" v-if="article.cover.type > 0">
                <upload-cover
                  v-for="(cover, index) in article.cover.type"
                  :key="cover"
                  @update-cover="onUpdateCover(index, $event)"
                  :cover-image="article.cover.images[index]"
                ></upload-cover> -->
              <div class="upload-cover-warp" v-if="article.cover.type > 0">
                <upload-cover v-for="(cover, index) in article.cover.type" :key="cover" v-model="article.cover.images[index]"></upload-cover>
              </div>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onPublish(false)">{{ $route.query.id ? '' : '' }}</el-button>
              <el-button @click="onPublish(true)"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </div>
    <!--  -->
    <div class="article-show-card">
      <div class="article-show"></div>
      <el-card shadow="never">
        <div class="article-title">
          {{ article.title || '' }}
          <div class="article-author">
            <div class="author-cover">
              <img class="author-cover-img" :src="user.photo ? user.photo : 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'" alt="" />
              <span class="author-name">{{ user.name }}</span>
            </div>
          </div>
        </div>
        <div class="article-content">
          <div class="ql-editor markdown-body" v-html="article.content || ''"></div>
        </div>
      </el-card>
      <div class="article-case">
        <!--  -->
        <div v-if="article.cover.type === 0" class="case-warp case-none">{{ article.title || '' }}</div>
        <!--  -->
        <div v-if="article.cover.type === -1" class="case-warp case-one">
          <div class="text-warp">{{ article.title || '' }}</div>
          <div class="img-warp">
            <img :src="article.cover.images[0]" />
          </div>
        </div>
        <!--  -->
        <div v-if="article.cover.type === 1" class="case-warp case-one">
          <div class="text-warp">{{ article.title || '' }}</div>
          <div class="img-warp">
            <img :src="article.cover.images[0]" />
          </div>
        </div>
        <!--  -->
        <div v-if="article.cover.type === 3" class="case-warp case-three">
          <div class="text-warp">{{ article.title || '' }}</div>
          <div class="img-warp">
            <div class="img-content"><img :src="article.cover.images[0]" /></div>
            <div class="img-content"><img :src="article.cover.images[1]" /></div>
            <div class="img-content"><img :src="article.cover.images[2]" /></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user'
import { getArticleChanels, addArticle, getArticle, updateArticle } from '@/api/article'
import { uploadImage } from '@/api/image'
import {
  ElementTiptap,
  //  extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Link,
  Image,
  CodeBlock,
  Blockquote,
  TodoItem,
  TodoList,
  TextAlign,
  FontSize,
  FontType,
  Fullscreen,
  TextHighlight,
  TextColor,
  FormatClear,
  Table,
  TableHeader,
  TableCell,
  TableRow,
  History,
  TrailingNode,
  HardBreak,
  HorizontalRule,
  LineHeight,
  Indent
} from 'element-tiptap'
import UploadCover from './components/upload-cover'
import './github-markdown.css'
export default {
  name: 'PublishIndex',
  data() {
    return {
      articleRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' },
          {
            // 
            validateContent(rule, value, callback) {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            }
          }
        ],
        channel_id: [{ required: true, message: '' }]
      },
      user: {},
      article: {
        title: '', // 
        content: '', // 
        // 
        cover: {
          type: 0, // (-1 ,0 ,1 ,3 )
          images: [] // 
        },
        channel_id: null // id
      },
      channels: [], // 
      extensions: [
        new Doc(), // 
        new Text(), // 
        new Paragraph(), // 
        new Heading({ level: 6 }), // 
        new Bold({ bubble: true }), //  bubble: true 
        new Underline({ bubble: true, menubar: false }), //  bubble: true, menubar: false 
        new Italic({ bubble: true }), // 
        new Strike({ bubble: true }), // 
        new ListItem(), // 
        new BulletList({ bubble: true }), // 
        new OrderedList({ bubble: true }), // 
        new Link(), // 
        new Image({
          // (base64)
          //  Promise<url>
          uploadRequest(file) {
            //  Content-Type  multipart/form-data FormData
            const fd = new FormData()
            fd.append('image', file)
            // url
            return uploadImage(fd).then((res) => {
              return res.data.data.url
            })
          }
        }), // 
        new CodeBlock({ bubble: true }), // 
        new Blockquote(), // 
        new TodoItem(), // 
        new TodoList(), // 
        new TextAlign({ bubble: true }), // 
        new FontSize({ bubble: true }), // 
        new FontType({ bubble: true }), // 
        new Fullscreen(), // 
        new TextHighlight({ bubble: true }), // 
        new TextColor({ bubble: true }), // 
        new FormatClear({ bubble: true }), // 
        new Table({ resizable: true }), // 
        new TableHeader(), // 
        new TableCell(), // 
        new TableRow(), // 
        new History(), // 
        new TrailingNode(), // 
        new HardBreak(), // 
        new HorizontalRule(), // 
        new LineHeight(), // 
        new Indent() // 
      ]
    }
  },
  created() {
    this.loadUserProfile()
    this.loadChannels()

    // id
    if (this.$route.query.id) {
      this.loadArticles()
    }
  },
  methods: {
    // 
    loadUserProfile() {
      getUserProfile().then((res) => {
        this.user = res.data.data
      })
    },
    // 
    loadChannels() {
      getArticleChanels().then((res) => {
        this.channels = res.data.data.channels
      })
    },
    // /
    onPublish(draft = false) {
      // 
      this.$refs['publish-from'].validate((valid) => {
        if (!valid) {
          // 
          return 0
        } else {
          // 
          if (this.$route.query.id) {
            // 
            const articleId = this.$route.query.id
            this.$confirm(draft ? '' : '', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              // 
              updateArticle(articleId, this.article, draft)
                .then(() => {
                  this.$notify({
                    title: '',
                    message: draft ? '' : '',
                    type: 'success'
                  })
                  // 
                  this.$router.push('/article')
                })
                .catch(() => {
                  this.$notify({
                    title: '',
                    message: '',
                    type: 'error'
                  })
                })
            })
          } else {
            this.$confirm(draft ? '' : '', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              // 
              addArticle(this.article, draft)
                .then(() => {
                  this.$notify({
                    title: '',
                    message: draft ? '' : '',
                    type: 'success'
                  })
                  // 
                  this.$router.push('/article')
                })
                .catch(() => {
                  this.$notify({
                    title: '',
                    message: '',
                    type: 'error'
                  })
                })
            })
          }
        }
      })
    },
    // ()
    loadArticles() {
      getArticle(this.$route.query.id).then((res) => {
        this.article = res.data.data
      })
    },
    // url
    onUpdateCover(index, url) {
      // urlimages
      this.article.cover.images[index] = url
    }
  },
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  }
}
</script>

<style lang="less" scoped>
.publish-container {
  // height: 100%;
  height: 90vh;
  display: flex;
  .article-pub-card {
    flex: 6;
    margin-right: 10px;
    // overflow-y: auto;
    // height: 90vh;
    height: 100%;

    .tiptap-warp {
      height: 55vh;
      padding-bottom: 5px;
      // background-color: orange;
    }
    /deep/.el-tiptap-editor .border-top-radius {
      border-radius: 5px 5px 0 0;
      border-color: #dcdfe6;
    }
    /deep/.el-tiptap-editor > .el-tiptap-editor__content {
      padding: 10px 15px;
      border-color: #dcdfe6;
      border-bottom: 1px solid #dcdfe6;
      margin-bottom: 5px;
      border-radius: 0 0 5px 5px;
    }
    .upload-cover-warp {
      display: flex;
    }
  }

  .article-show-card {
    flex: 3;
    // height: 90vh;
    height: 97%;
    position: relative;
    overflow: auto;
    overflow: hidden;
    padding: 10px 0 250px 0;
    background-color: #fff;
    border-radius: 5px;
    /deep/.el-card__body {
      padding: 0 10px;
    }
    .el-card {
      border: none;
      border-radius: 5px;
      overflow-y: auto;
      margin-top: 80px;
      padding-top: 0px;
      height: 100%;
    }
    .article-show {
      padding: 5px;
      font-size: 17px;
      border-left: 3px solid var(--themeColor);
    }
    .article-title {
      position: absolute;
      top: 50px;
      left: 0;
      width: 100%;
      font-size: 18px;
      padding: 5px 20px;
      font-weight: 700;
      background-color: #fff;
      .article-author {
        .author-cover {
          margin-top: 5px;
          display: flex;
          align-items: center;
          .author-cover-img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            margin-right: 5px;
          }
          span {
            font-size: 16px;
            font-weight: 400;
          }
        }
      }
    }
    .article-content {
      padding: 10px;
      // padding-top: 40px;
      overflow: hidden;
    }
  }
  .article-case {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 150px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    .case-warp {
      width: 80%;
      display: flex;
      background-color: #fff;
      border-radius: 10px;
      padding: 10px 10px;
      // 
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
    }
    .case-none {
      height: 60px;
      align-items: center;
    }
    .case-one {
      width: 80%;
      height: 100px;
      display: flex;
      .text-warp {
        flex: 3;
        margin-right: 8px;
      }
      .img-warp {
        flex: 1.5;
        height: 100%;
        overflow: hidden;
        background-color: #dcdcdc;
        img {
          height: 100%;
          background-size: cover;
        }
      }
    }
    .case-three {
      height: 120px;
      display: flex;
      flex-direction: column;
      .text-warp {
        flex: 1;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .img-warp {
        flex: 3;
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        margin-top: 5px;
        .img-content {
          flex: 1;
          width: 33%;
          height: 100%;
          margin-right: 5px;
          background-color: #dcdcdc;
          overflow: hidden;
          background-size: cover;
          img {
            width: 100%;
            // height: 100%;
          }
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
</style>
