<template>
  <div class="comment-container">
    <el-card class="box-card" shadow="never">
      <div slot="header" class="clearfix">
        <span>
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </span>
      </div>
      <div>
        <el-table :data="articles" style="width: 100%" v-loading="isLoading">
          <el-table-column prop="title" label=""> </el-table-column>
          <el-table-column prop="total_comment_count" label=""> </el-table-column>
          <el-table-column prop="fans_comment_count" label=""> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tag size="small" :type="scope.row.comment_status ? 'success' : 'danger'">{{ scope.row.comment_status ? '' : '' }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.comment_status"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                :disabled="scope.row.isDisabled"
                @change="onStatusChange(scope.row)"
              >
              </el-switch>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!--  -->
      <el-pagination
        class="list-pagination"
        background
        @size-change="onSizeChange"
        @current-change="onCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size.sync="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount"
        :disabled="isLoading"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { getArticles, updateCommentStatus } from '@/api/article'
export default {
  name: 'CommentIndex',
  data() {
    return {
      articles: [], // 
      totalCount: 0, // 
      pageSize: 10, // 
      currentPage: 1, // 
      isLoading: false // loading
    }
  },
  created() {
    this.loadArticles()
  },
  methods: {
    // 
    loadArticles(page = 1) {
      // loading
      this.isLoading = true
      // 
      this.currentPage = page
      getArticles({
        response_type: 'comment',
        page, // 
        per_page: this.pageSize // 
      }).then((res) => {
        // loading
        this.isLoading = false
        const { results, total_count: totalCount } = res.data.data
        // 
        results.forEach((article) => {
          article.isDisabled = false
        })
        this.articles = results
        this.totalCount = totalCount
      })
    },
    // 
    onStatusChange(article) {
      // 
      article.isDisabled = true
      updateCommentStatus(article.id.toString(), article.comment_status).then((res) => {
        // 
        article.isDisabled = false
        this.$notify({
          title: '',
          message: article.comment_status ? '' : '',
          type: 'success'
        })
      })
    },
    // 
    onSizeChange(pageSize) {
      this.pageSize = pageSize
      this.loadArticles(1)
    },
    // 
    onCurrentChange(page) {
      this.currentPage = page
      this.loadArticles(this.currentPage)
    }
  }
}
</script>

<style lang="less" scoped>
.comment-container {
  .el-card {
    border: none;
    border-radius: 5px;
    /deep/.el-table th.el-table__cell.is-leaf {
      &:nth-child(n + 2) {
        text-align: center;
      }
    }
    /deep/.el-table--enable-row-transition .el-table__body td.el-table__cell {
      &:nth-child(n + 2) {
        text-align: center;
      }
      // &:first-child {
      //   div {
      //     overflow: hidden;
      //     white-space: nowrap;
      //     text-overflow: ellipsis;
      //   }
      // }
    }
  }

  .list-pagination {
    margin-top: 20px;
    text-align: right;
  }
}
</style>
