import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '@/views/login'
import Home from '@/views/home'
import Layout from '@/views/layout'
import Article from '@/views/article'
import Publish from '@/views/publish'
import Image from '@/views/image'
import Comment from '@/views/comment'
import Settings from '@/views/settings'
import Fans from '@/views/fans'

Vue.use(VueRouter)

// 
const routes = [
  // { path: '/', redirect: '/login' },
  { path: '/login', name: 'login', component: Login },
  {
    path: '/',
    // name: 'layout', // name
    component: Layout,
    children: [
      {
        path: '', // path
        name: 'home',
        component: Home
      },
      {
        path: '/article',
        name: 'article',
        component: Article
      },
      {
        path: '/publish',
        name: 'publish',
        component: Publish
      },
      {
        path: '/image',
        name: 'image',
        component: Image
      },
      {
        path: '/comment',
        name: 'comment',
        component: Comment
      },
      {
        path: '/settings',
        name: 'settings',
        component: Settings
      },
      {
        path: '/fans',
        name: 'fans',
        component: Fans
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 
router.beforeEach((to, from, next) => {
  // hash to.path
  // hash /login
  if (to.path !== '/login') {
    // ()
    // localStoragetoken(localStorage.getItem('user'))
    const user = localStorage.getItem('user')
    if (user) {
      next() // user
    } else {
      next('/login') // user
    }
  } else {
    // 
    next()
  }
})

export default router
