<template>
  <div class="article-container">
    <!--  -->
    <el-card class="filter-warp" shadow="never">
      <div slot="header" class="clearfix">
        <span>
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </span>
      </div>
      <div>
        <!--  -->
        <el-form ref="form" :model="form" label-width="50px" size="small">
          <el-form-item label="">
            <el-radio-group v-model="status">
              <el-radio :label="null"></el-radio>
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
              <el-radio :label="4"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="channelId" placeholder="">
              <el-option label="" :value="null"></el-option>
              <el-option v-for="channel in channels" :key="channel.id" :label="channel.name" :value="channel.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="rangeDate"
              type="daterange"
              align="center"
              unlink-panels
              range-separator="-"
              start-placeholder=""
              end-placeholder=""
              :picker-options="form.pickerOptions"
              value-format="yyyy-MM-dd"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="loading" @click="loadArticles(1)"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!--  -->
    <el-card class="box-card" shadow="never">
      <div>
        <!--  -->
        <el-table class="list-table" :data="articles" style="width: 100%" v-loading="loading">
          <el-table-column label="">
            <template slot-scope="scope">
              <div class="article-cover">
                <el-image v-if="scope.row.cover.images[0]" :src="scope.row.cover.images[0]" fit="cover" lazy>
                  <div slot="placeholder" class="image-slot"><span class="dot">...</span></div>
                </el-image>
                <el-image v-else src="" fit="cover" lazy> <div slot="error" class="image-slot"></div></el-image>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tag size="medium" :type="articleStatus[scope.row.status].type">{{ articleStatus[scope.row.status].text }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <i class="el-icon-time"></i>
              <span style="margin-left: 10px">{{ scope.row.pubdate }}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top-start">
                <el-button type="primary" plain size="mini" icon="el-icon-edit" circle @click="$router.push('/publish?id=' + scope.row.id)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top-start">
                <el-button type="danger" plain size="mini" icon="el-icon-delete" circle @click="onDeleteArticle(scope.row.id)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          class="list-pagination"
          background
          @current-change="onCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="totalCount"
          :hide-on-single-page="true"
          :disabled="loading"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import { getArticles, getArticleChanels, deleteArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  data() {
    return {
      form: {
        pickerOptions: {
          shortcuts: [
            {
              text: '',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
                picker.$emit('pick', [start, end])
              }
            },
            {
              text: '',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
                picker.$emit('pick', [start, end])
              }
            },
            {
              text: '',
              onClick(picker) {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
                picker.$emit('pick', [start, end])
              }
            }
          ]
        }
      },
      articles: [], // 
      // 
      articleStatus: [
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '', type: 'warning' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'danger' },
        { status: 4, text: '', type: 'danger' }
      ],
      currentPage: 1, // 
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, // 
      channels: [], // 
      channelId: null, // id
      rangeDate: null, // 
      loading: true // loading
    }
  },
  created() {
    this.loadChannels()
    this.loadArticles()
  },
  methods: {
    // 
    loadArticles(page = 1) {
      // loading
      this.loading = true
      getArticles({
        page, // 
        per_page: this.pageSize, // 
        status: this.status, // 
        channel_id: this.channelId, // id
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then((res) => {
        // loading
        this.loading = false
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount
      })
    },
    // 
    onCurrentChange(page) {
      this.currentPage = page
      // 
      this.loadArticles(page)
    },
    // 
    loadChannels() {
      getArticleChanels().then((res) => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onDeleteArticle(articleId) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        deleteArticle(articleId.toString()).then((res) => {
          // 
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          // 
          this.loadArticles(this.currentPage)
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.article-container {
  .el-card {
    border: none;
    border-radius: 5px;
  }
  .filter-warp {
    margin-bottom: 10px;
  }
  .list-table {
    margin-bottom: 20px;
    .article-cover {
      width: 200px;
      height: 100px;
      overflow: hidden;
      .el-image {
        width: 100%;
        height: 100%;
        /deep/.image-slot {
          line-height: 100px;
          text-align: center;
          // background-color: #f1f3f4;
          background-color: #f5f7fa;
          color: #c0c4cc;
        }
      }
    }
  }
  /deep/.el-table th.el-table__cell.is-leaf {
    &:nth-child(n + 3) {
      text-align: center;
    }
  }
  /deep/.el-table--enable-row-transition .el-table__body td.el-table__cell {
    &:nth-child(n + 3) {
      text-align: center;
    }
  }
  .list-pagination {
    text-align: right;
  }
}
</style>
