// 
import request from '@/utils/request'

// 
export const getFans = (data) => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/followers',
    data
  })
}

// 
export const getFansData = () => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/statistics/followers'
  })
}
