<template>
  <div class="upload-cover-container">
    <!-- <div class="cover-warp">+</div> -->
    <!-- <div class="cover-warp" @click="showCoverSelect"> -->
    <!-- <img class="cover-image" src="https://z3.ax1x.com/2021/11/01/IPG7QK.jpg" alt="" /> -->
    <!-- </div> -->
    <div class="cover-warp" @click="showCoverSelect"><img class="cover-image" ref="cover-image" :src="value" alt="" />{{ value ? '' : '+' }}</div>
    <!--  -->
    <el-dialog title="" :visible.sync="coverSelectDialog" width="50%" :append-to-body="true" @closed="resetUploadImage">
      <span>
        <el-tabs v-model="activeName" type="card">
          <el-tab-pane label="" name="first">
            <image-list :is-show-add="false" :is-show-action="false" ref="image-list"></image-list>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <div class="upload-img-warp">
              <div class="upload-btns">
                <input type="file" name="" ref="upload-cover" hidden @change="onFileChange" />
                <el-button size="small" type="primary" @click="$refs['upload-cover'].click()"></el-button>
              </div>
              <div class="preview-cover-warp">
                <div v-if="!previewImage"></div>
                <img v-else class="preview-cover" :src="previewImage" alt="" />
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="coverSelectDialog = false"> </el-button>
        <el-button size="small" type="primary" @click="onUploadCover" :loading="updateCoverLoading"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { uploadImage } from '@/api/image'
import ImageList from '@/views/image/components/image-list'
export default {
  name: 'UploadCover',
  props: ['value'],
  data() {
    return {
      coverSelectDialog: false, // dialog
      activeName: 'first',
      previewImage: '', // 
      updateCoverLoading: false // loading
    }
  },
  components: {
    ImageList
  },
  methods: {
    showCoverSelect() {
      this.coverSelectDialog = true
    },
    // -
    onFileChange() {
      const file = this.$refs['upload-cover']
      //  file.files[0]URL
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // change
      // file.value = ''
    },
    // -
    onUploadCover() {
      // 
      // 
      if (this.activeName === 'second') {
        const file = this.$refs['upload-cover'].files[0]
        if (!file) {
          this.$notify({
            title: '',
            message: '',
            type: 'warning'
          })
          return 0
        }
        // 
        // loading
        this.updateCoverLoading = true
        const fd = new FormData()
        fd.append('image', file)
        uploadImage(fd)
          .then((res) => {
            this.$refs['cover-image'].src = res.data.data.url
            // loading
            this.updateCoverLoading = false
            // dialog
            this.coverSelectDialog = false
            // 
            this.$emit('input', res.data.data.url)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          })
          .catch(() => {
            // loading
            this.updateCoverLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          })
      } else if (this.activeName === 'first') {
        // 
        //  ref
        const imageList = this.$refs['image-list']
        const selected = imageList.selected
        if (selected === null) {
          this.$notify({
            title: '',
            message: '',
            type: 'warning'
          })
          return 0
        }
        // dialog
        this.coverSelectDialog = false
        // 
        this.$emit('input', imageList.images[selected].url)
      }
    },
    // dialog
    resetUploadImage() {
      // 
      this.previewImage = ''
    }
  }
}
</script>

<style lang="less" scoped>
.upload-cover-container {
  margin-top: 10px;
  .cover-warp {
    width: 170px;
    height: 110px;
    border: 1px dashed #dcdfe6;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #dcdfe6;
    font-size: 25px;
    margin-right: 10px;
    overflow: hidden;
    .cover-image {
      height: 100%;
    }
  }
}
.upload-img-warp {
  display: flex;
  flex-direction: column;
  .upload-btns {
    display: flex;
    justify-content: right;
  }
  .preview-cover-warp {
    height: 200px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .preview-cover {
    max-width: 100%;
    height: 100%;
  }
}
</style>
