<template>
  <div class="login-container">
    <div class="login-warp">
      <el-form class="login-form" ref="login-form" :model="user" :rules="formRoules" label-width="0px">
        <div class="login-text"></div>
        <el-form-item prop="mobile">
          <el-input v-model="user.mobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="user.code" placeholder=""></el-input>
        </el-form-item>
        <!-- <el-checkbox class="user-notice" v-model="user.checked"></el-checkbox> -->
        <div class="user-notice"></div>
        <el-form-item>
          <el-button class="login-btn" type="primary" @click="onLogin" @keydown.enter="onLogin" :loading="loginLoading"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { userLogin } from '@/api/user'
export default {
  name: 'LoginIndex',
  data() {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810' // 
      },
      // user: {
      //   mobile: '', // 
      //   code: '' // 
      // },
      loginLoading: false, // Loading
      // 
      formRoules: {
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3|5|7|8|9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{6}$/, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted() {
    this.$notify({
      title: '',
      dangerouslyUseHTMLString: true,
      message: '<div>13911111111</div><div>246810</div>'
    })
  },
  methods: {
    // 
    async onLogin() {
      // ()
      this.$refs['login-form'].validate((valid) => {
        if (valid) {
          // -
          this.login()
        } else {
          // 
          return false
        }
      })
    },
    // 
    login() {
      userLogin(this.user)
        .then((res) => {
          // 
          // ()
          // JSONJSON.stringify(xxx)
          window.localStorage.setItem('user', JSON.stringify(res.data.data))
          // 
          this.$router.push({ name: 'home' })
          // 
          this.$notify({
            title: res.data.data.name,
            message: ''
          })
        })
        .catch(() => {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('./bg2.png') no-repeat;
  background-color: #b8e5f8;
  background-size: cover;
  .login-warp {
    box-shadow: 0 1px 3px rgb(18 18 18 / 10%);
    border-radius: 5px;
    background-color: #ffffff;
    width: 370px;
    overflow: hidden;
    .login-text {
      font-size: 22px;
      margin-bottom: 20px;
      font-weight: 500;
    }
    .login-form {
      background-color: #fff;
      margin: 30px;
      // overflow: hidden;
      padding-bottom: 0;
      /deep/.el-input__inner {
        border: none;
        border-bottom: 1px solid #ebebeb;
        padding: 0;
        border-radius: 0;
        font-size: 15px;
        &:focus {
          border-bottom: 1px solid #06f;
        }
      }
      /deep/.el-form-item.is-error .el-input__inner {
        border-color: #f56c6c;
      }
      .user-notice {
        font-size: 13px;
        color: #808080;
        padding: 5px 0 20px 0;
      }
      .login-btn {
        width: 100%;
        color: #fff;
        background-color: #06f;
      }
    }
  }
}
</style>
