<template>
  <div class="settings-container">
    <el-card class="box-card" shadow="never">
      <div slot="header" class="clearfix">
        <span>
          <!--  -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </span>
      </div>
      <div>
        <el-tabs activeName="first">
          <el-tab-pane label="" name="first" class="user-warp">
            <div class="user user-id">
              <span class="user-warp-title">ID</span>
              <span class="user-warp-content">
                <span>{{ user.id }}</span>
              </span>
            </div>
            <div class="user user-phone">
              <span class="user-warp-title"></span>
              <span class="user-warp-content">
                <span>{{ user.mobile }}</span>
              </span>
            </div>
            <div class="user user-name">
              <span class="user-warp-title"></span>
              <span class="user-warp-content">
                <span>{{ user.name ? user.name : '' }}</span>
                <span class="tool-warp" @click="dialogUserNameVisible = true"></span>
              </span>
            </div>
            <div class="user user-desc">
              <span class="user-warp-title"></span>
              <span class="user-warp-content">
                <span>{{ user.intro ? user.intro : '' }}</span>
                <span class="tool-warp" @click="dialogUserDescVisible = true"></span>
              </span>
            </div>
            <div class="user user-avator">
              <span class="user-warp-title"></span>
              <span class="user-warp-content">
                <div class="avator-content">
                  <img
                    :src="user.photo ? user.photo : 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'"
                    alt=""
                    @click="$refs.uploadAvator.click()"
                  />
                  <div class="add-avator" @click="$refs.uploadAvator.click()">{{ user.photo ? '' : '' }}</div>
                </div>
                <input type="file" ref="uploadAvator" hidden @change="onFileChange" />
              </span>
            </div>
            <div class="user user-email">
              <span class="user-warp-title"></span>
              <span class="user-warp-content">
                <span>{{ user.email ? user.email : '' }}</span>
                <span class="tool-warp" @click="dialogUserEmailVisible = true">{{ user.email ? '' : '' }}</span>
              </span>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-card>
    <!--  -->
    <el-dialog
      class="dirlogs"
      title=""
      :visible.sync="dialogUserNameVisible"
      :append-to-body="true"
      width="450px"
      :destroy-on-close="true"
      @closed="resetForm"
    >
      <el-form ref="form" :model="previewUser" size="medium" :rules="rules">
        <el-form-item prop="name">
          <el-input v-model="previewUser.name" placeholder="" maxlength="7" show-word-limit></el-input>
          <p></p>
        </el-form-item>
      </el-form>
      <div class="btns">
        <el-button size="small" @click="dialogUserNameVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="onUpdateUser(1)"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="dirlogs user-desc-dirlog"
      title=""
      :visible.sync="dialogUserDescVisible"
      :append-to-body="true"
      width="450px"
      @closed="resetForm"
    >
      <el-form ref="form" :model="previewUser" size="medium">
        <el-form-item>
          <el-input type="textarea" placeholder="" v-model="previewUser.intro" maxlength="70" show-word-limit></el-input>
          <p></p>
        </el-form-item>
      </el-form>
      <div class="btns">
        <el-button size="small" @click="dialogUserDescVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="onUpdateUser(2)"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="dirlogs user-desc-dirlog"
      title=""
      :visible.sync="dialogUserAvatorVisible"
      :append-to-body="true"
      width="500px"
      @opened="onAvatarDialogOpened"
    >
      <div class="avator-preview">
        <img :src="previewImage" ref="preview-image" alt="" />
      </div>
      <div class="btns">
        <el-button size="small" @click="dialogUserAvatorVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="onUpdateAvatar" :loading="updateAvatarLoading"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="dirlogs user-desc-dirlog"
      title=""
      :visible.sync="dialogUserEmailVisible"
      :append-to-body="true"
      width="450px"
      @closed="resetForm"
    >
      <el-form ref="form" :model="previewUser" :rules="rules" size="medium">
        <el-form-item prop="email">
          <el-input v-model="previewUser.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div class="btns">
        <el-button size="small" @click="dialogUserEmailVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="onUpdateUser(3)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getUserProfile, updateUserAvatar, updateUserProfile } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'

export default {
  name: 'SettingsIndex',
  data() {
    return {
      dialogUserNameVisible: false, // 
      dialogUserDescVisible: false, // 
      dialogUserAvatorVisible: false, // 
      dialogUserEmailVisible: false, // 
      updateAvatarLoading: false, // loading
      updateUsernameLoading: false, // loading
      updateIntroLoading: false, // loading
      updateEmailLoading: false, // loading

      // 
      user: {
        id: null, // id
        mobile: '', // 
        name: '', // 
        intro: '', // 
        photo: '', // 
        email: '' // 
      },
      previewUser: {
        name: '', // 
        intro: '', // 
        email: '' // 
      },
      previewImage: '', // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        email: [{ type: 'email', message: '', trigger: 'blur' }]
      },
      cropper: null // 
    }
  },
  created() {
    this.loadUser()
  },
  methods: {
    // 
    loadUser() {
      getUserProfile().then((res) => {
        this.user = res.data.data
        const { name, intro, email } = this.user
        this.previewUser.name = name
        this.previewUser.intro = intro
        this.previewUser.email = email
      })
    },
    // -
    onFileChange() {
      const file = this.$refs.uploadAvator
      //  file.files[0]URL
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // change
      file.value = ''
      // 
      this.dialogUserAvatorVisible = true
    },
    // 
    onAvatarDialogOpened() {
      // 1.replace
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return
      }
      // img
      // DOM
      const image = this.$refs['preview-image']
      // ()
      // ()1..replace() 2. .destroy()
      this.cropper = new Cropper(image, {
        // aspectRatio  1/1  1
        // aspectRatio: 1 / 1
        aspectRatio: 1,
        //  viewMode: 1 
        viewMode: 1,
        //  none/move
        // dragMode: 'none',
        // 
        background: false
      })
    },
    // 
    onUpdateAvatar() {
      // loading
      this.updateAvatarLoading = true
      // 
      this.cropper.getCroppedCanvas().toBlob((file) => {
        // 
        const fd = new FormData()
        fd.append('photo', file)
        updateUserAvatar(fd)
          .then((res) => {
            // loading
            this.updateAvatarLoading = false
            // 
            this.dialogUserAvatorVisible = false
            // 
            // this.user.photo = res.data.data.photo
            this.user.photo = window.URL.createObjectURL(file)
            // layout
            globalBus.$emit('update-user', this.user)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          })
          .catch(() => {
            // loading
            this.updateAvatarLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          })
      })
    },
    // 
    onUpdateUser(type) {
      const { name, intro, email } = this.previewUser
      if (type === 1) {
        // loading
        this.updateUsernameLoading = true
        // 
        updateUserProfile({ name })
          .then((res) => {
            // loading
            this.updateUsernameLoading = false
            // 
            this.dialogUserNameVisible = false
            // 
            this.user.name = name
            // layout
            globalBus.$emit('update-user', this.user)
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          })
          .catch(() => {
            // loading
            this.updateUsernameLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          })
      } else if (type === 2) {
        // 
        // loading
        this.updateIntroLoading = true
        // 
        updateUserProfile({ intro })
          .then(() => {
            // loading
            this.updateIntroLoading = false
            // 
            this.dialogUserDescVisible = false
            // 
            this.user.intro = intro
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          })
          .catch(() => {
            // loading
            this.updateIntroLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          })
      } else if (type === 3) {
        // 
        // loading
        this.updateEmailLoading = true
        // 
        updateUserProfile({ email })
          .then(() => {
            // loading
            this.updateEmailLoading = false
            // 
            this.dialogUserEmailVisible = false
            // 
            this.user.email = email
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            })
          })
          .catch(() => {
            // loading
            this.updateEmailLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          })
      }
    },
    // dialog
    resetForm() {
      const { name, intro, email } = this.user
      this.previewUser.name = name
      this.previewUser.intro = intro
      this.previewUser.email = email
    }
  }
}
</script>

<style lang="less" scoped>
.settings-container {
  .el-card {
    border: none;
    border-radius: 5px;
    /deep/.el-tabs__active-bar {
      background-color: var(--themeColor);
      width: 64px !important;
    }
    /deep/.el-tabs__item.is-active {
      font-size: 16px;
      color: var(--themeColor);
    }
    /deep/.el-tabs__nav-wrap::after {
      background-color: transparent;
    }
    /deep/.el-tabs__content {
      padding: 20px 55px;
    }
    .user-warp {
      width: 100%;
      .user {
        font-size: 15px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        padding: 25px 0;
        .user-warp-title {
          width: 10%;
        }
        .user-warp-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          color: #666;
          position: relative;
          .tool-warp {
            color: #1356bd;
            &:hover {
              cursor: pointer;
            }
          }
        }
      }
      .avator-content {
        position: relative;
        width: 110px;
        height: 110px;
        border-radius: 50%;
        overflow: hidden;
        img {
          height: 100%;
          &:hover {
            cursor: pointer;
          }
        }
        .add-avator {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 25px;
          background: rgba(0, 0, 0, 0.4);
          line-height: 25px;
          text-align: center;
          color: #fff;
          font-size: 12px;
          &:hover {
            cursor: pointer;
          }
        }
      }
    }
  }
}
.dirlogs {
  p {
    color: #acacac;
  }
  .btns {
    text-align: right;
  }
}
.user-desc-dirlog {
  /deep/.el-textarea__inner {
    height: 150px;
    resize: none;
    font-family: '';
  }
}
.avator-preview {
  width: 100%;
  margin-bottom: 20px;
  img {
    max-width: 100%;
    height: 200px;
  }
}
</style>
